import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    fetchUsers(ctx, queryParams) {
      return new Promise((resolve, reject) => {
        axios
          .get('/atenciones', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchAtencion(ctx, { id }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/atenciones/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    finalizarAtencion(ctx, atencionData) {
      return new Promise((resolve, reject) => {
        axios
          .put(`/atenciones/${atencionData.id}`, atencionData)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    addUser(ctx, userData) {
      return new Promise((resolve, reject) => {
        axios
          .post('/atenciones', { user: userData })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
