import { ref, watch, computed } from '@vue/composition-api'
// import { title } from '@core/utils/filter'

// Notification
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import store from '@/store'

export default function useUsersList() {
  // Use toast
  const toast = useToast()

  const refUserListTable = ref(null)

  // Table Handlers
  const tableColumns = [
    { key: 'usuario', label: 'paciente', sortable: false },
    // { key: 'email', sortable: false },
    // { key: 'role', label: 'servicio', sortable: false },
    { key: 'servicio.nombre', label: 'servicio', sortable: false },
    // {
    //   key: 'currentPlan',
    //   label: 'Plan',
    //   formatter: title,
    //   sortable: false,
    // },
    { key: 'created_at', label: 'fecha atencin', sortable: false },
    { key: 'status', label: 'estado', sortable: false },
    { key: 'actions', label: 'accin' },
  ]
  const perPage = ref(10)
  const totalUsers = ref(0)
  const currentPage = ref(1)
  const perPageOptions = [10, 25, 50, 100]
  const searchQuery = ref('')
  const sortBy = ref('id')
  const isSortDirDesc = ref(true)
  const roleFilter = ref(null)
  const planFilter = ref(null)
  const estadoFilter = ref(null)
  const desdeHastaFilter = ref('')
  const desdeFilter = ref('')
  const hastaFilter = ref('')

  const dataMeta = computed(() => {
    const localItemsCount = refUserListTable.value ? refUserListTable.value.localItems.length : 0
    return {
      from: perPage.value * (currentPage.value - 1) + (localItemsCount ? 1 : 0),
      to: perPage.value * (currentPage.value - 1) + localItemsCount,
      of: totalUsers.value,
    }
  })

  const refetchData = () => {
    refUserListTable.value.refresh()
  }

  watch([currentPage, perPage, searchQuery, roleFilter, planFilter, estadoFilter, desdeHastaFilter], () => {
    desdeFilter.value = desdeHastaFilter.value.substring(0, 10)
    hastaFilter.value = desdeHastaFilter.value.substring(14, 24)
    refetchData()
  })

  const fetchUsers = (ctx, callback) => {
    store
      .dispatch('app-user/fetchUsers', {
        filter: searchQuery.value,
        perPage: perPage.value,
        page: currentPage.value,
        sortBy: sortBy.value,
        sortDesc: isSortDirDesc.value,
        role: roleFilter.value,
        plan: planFilter.value,
        estado: estadoFilter.value,
        desde: desdeFilter.value,
        hasta: hastaFilter.value,
      })
      .then(response => {
        console.log(response)
        const { data, total } = response.data
        callback(data)
        totalUsers.value = total
      })
      .catch(() => {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Error fetching users list',
            icon: 'AlertTriangleIcon',
            variant: 'danger',
          },
        })
      })
  }

  // *===============================================---*
  // *--------- UI ---------------------------------------*
  // *===============================================---*

  const resolveUserRoleVariant = role => {
    if (role === 'psicologia') return 'primary'
    if (role === 'pendiente') return 'warning'
    if (role === 'oftamologia') return 'success'
    if (role === 'odontologia') return 'info'
    if (role === 'gineco-obstetrico') return 'danger'
    return 'primary'
  }

  const resolveUserRoleIcon = role => {
    if (role === 'psicologia') return 'UserIcon'
    if (role === 'medicina') return 'SettingsIcon'
    if (role === 'oftamologia') return 'DatabaseIcon'
    if (role === 'odontologia') return 'Edit2Icon'
    if (role === 'gineco-obstetrico') return 'ServerIcon'
    return 'UserIcon'
  }

  const resolveUserStatusVariant = status => {
    if (status === 'pendiente') return 'warning'
    if (status === 'completado') return 'success'
    return 'primary'
  }

  return {
    fetchUsers,
    tableColumns,
    perPage,
    currentPage,
    totalUsers,
    dataMeta,
    perPageOptions,
    searchQuery,
    sortBy,
    isSortDirDesc,
    refUserListTable,

    resolveUserRoleVariant,
    resolveUserRoleIcon,
    resolveUserStatusVariant,
    refetchData,

    // Extra Filters
    roleFilter,
    planFilter,
    estadoFilter,
    desdeHastaFilter,
  }
}
